.project-home {
  height: calc(calc(1vh * 100) - 6.875rem - 5rem);
  height: calc(calc(var(--vh, 1vh) * 100) - 6.875rem - 5rem);
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 6.875rem;
}

.project-subtitle {
  margin-top: auto;
}

.project-first-image {
  position: relative;
  display: flex;
  width: 100%;
  padding: 8.25rem 0;
}

.project-first-image > * > img {
  margin: auto;
  height: auto;
  box-shadow: 0 0 0.625rem 0.3rem var(--BlackShadow);
}

.project-first-image > * > video {
  margin: auto;
  width: 100%;
  height: auto;
  min-height: 400px;
  object-fit: cover;
  margin: -5rem 0;
}

.project-second-image {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.project-description {
  padding: 8.25rem 0;
  display: flex;
  justify-content: space-between;
  grid-gap: 10rem;
  gap: 10rem;
}

.project-presentation-images {
  width: 100%;
  height: 100vh;
  max-height: 80rem;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.grid-image-container {
  display: grid;
  grid-template-columns: repeat(4, 35rem);
  justify-items: center;
  align-items: center;
  grid-auto-rows: 35rem;
  grid-template-areas: ". image1 . ." "image2 . image3 ." ". image4 . image5" ". . image6 .";
  -webkit-transform: rotate(-30deg);
          transform: rotate(-30deg);
}

.grid-image-container > img {
  width: 60rem;
  box-shadow: 0 0 0.625rem 0.3rem var(--BlackShadow);
}

.grid-image-container > img:nth-child(1) {
  grid-area: image1;
}
.grid-image-container > img:nth-child(2) {
  grid-area: image2;
}
.grid-image-container > img:nth-child(3) {
  grid-area: image3;
}
.grid-image-container > img:nth-child(4) {
  grid-area: image4;
}
.grid-image-container > img:nth-child(5) {
  grid-area: image5;
}
.grid-image-container > img:nth-child(6) {
  grid-area: image6;
}

.project-row {
  padding: 8.25rem 0 0 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  grid-gap: 10rem;
  gap: 10rem;
}

.project-row:nth-child(2) {
  padding: 0;
}

.project-row:last-child {
  padding: 8.25rem 0;
}

.project-row-image {
  min-width: 0;
  padding: 3.5rem;
  width: 100%;
}

.project-row-image-dual {
  width: 100%;
  height: auto;
  position: relative;
}
.project-row-image-dual > img {
  margin: 3.5rem 3.5rem calc(3.5rem + 10%) 3.5rem;
  width: 70%;
  box-shadow: 0 0 0.625rem 0.3rem var(--BlackShadow);
}
.project-row-image-dual > img:last-child {
  position: absolute;
  margin: 0;
  right: 3.5rem;
  bottom: 3.5rem;
  box-shadow: 0 0 0.625rem 0.3rem var(--BlackShadow);
}

.project-row-image > img {
  width: 100%;
  box-shadow: 0 0 0.625rem 0.3rem var(--BlackShadow);
}

.project-row:nth-child(odd) {
  flex-direction: row-reverse;
}
.project-row:first-child {
  flex-direction: row;
}

.explore-container {
  margin-top: auto;
  margin-bottom: 2.5rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  grid-gap: 0.75rem;
  gap: 0.75rem;
  cursor: pointer;
}

.explore-text {
  font-size: 2.5rem;
  font-weight: 400;
}

.explore-svg {
  -webkit-transform: translateY(10%);
          transform: translateY(10%);
  width: 2rem;
  height: 2.5rem;
  -webkit-animation: up-down 1.75s ease-in-out infinite;
          animation: up-down 1.75s ease-in-out infinite;
}

.project-page-menu {
  height: 6.875rem;
  position: fixed;
  top: 0;
  background-color: var(--Backgound);
  z-index: 2;
}

.project-page-menu-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.project-page-menu-back {
  width: 2rem;
  cursor: pointer;
  transition: all 100ms ease-out;
}

.project-page-menu-back:hover {
  -webkit-transform: scale(1.25);
          transform: scale(1.25);
}

.project-page-menu-link-container {
  margin-left: auto;
  display: flex;
  grid-gap: 5.25rem;
  gap: 5.25rem;
}

.project-page-menu-link {
  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
  align-items: center;
}
.project-page-menu-link > svg {
  width: 2.5rem;
  -webkit-transform: translate(0, -0.125rem);
          transform: translate(0, -0.125rem);
}

.footer {
  width: 100%;
  background-color: var(--Black);
  padding: 4.5rem 0rem;
}

.footer-container {
  display: flex;
  flex-direction: row;
  align-content: center;
  justify-content: space-between;
}

.footer-columns {
  display: flex;
  flex-direction: column;
  grid-gap: 1.125rem;
  gap: 1.125rem;
}

.footer-link {
  color: var(--White);
  font-size: 1.625rem;
  text-decoration: underline;
}

.footer-logo {
  width: 4.125rem;
  margin-bottom: 1rem;
}

